@font-face {
    font-family: 'Poppins-Medium';
    font-style: Medium;
    src: url('./fonts/Poppins/Poppins-Medium.ttf');
}
@font-face {
    font-family: 'Poppins-Light';
    font-style: Light;
    src: url('./fonts/Poppins/Poppins-Light.ttf');
}
@font-face {
    font-family: 'Poppins';
    font-style: normal;
    src: url('./fonts/Poppins/Poppins-Regular.ttf');
}

@mixin poppins14_500{
    font-family: 'Poppins-Medium';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 28px;
}

@mixin poppins16_300{
