.white{
    height: 2062px;
    border-radius: 32px;
    background-color: var(--white-color);
    &__firstText{
        padding-top: 260px;
        &__heading-wrap{
            max-width: 1080px;
            h1{
                @include poppins52();
                letter-spacing: 0.02em;
                color: var(--iris-color);
                display: inline;
            }
            img{
                display: inline;
                vertical-align: bottom;
            }
        }
        p{
            @include poppins16_300();
            letter-spacing: 0.04em;
            max-width: 910px;
            padding-top: 40px;
            color: var(--space-cadet-color);
        }
    }
    &__pictures{
        display: flex;
        gap: 24px;
        padding: 200px 36px 0px 36px;
        &__item{
            position: relative;
            width: 100%;
            height: 100%;
            &__overlay{
                cursor: pointer;
                position: absolute;
                background-color: var(--iris-color);
                mix-blend-mode: overlay;
                width: 100%;
                height: 100%;
                opacity: 0;
                transition: opacity 0.3s ease-out;
            }
            p{
                cursor: pointer;
                @include poppins16_500();
                letter-spacing: 0.1em;
                text-transform: uppercase;
                font-feature-settings: 'liga' off;
                position: absolute;
                bottom: 32px;
                left: 40px;
                color: #0000FF;
                opacity: 0;
                transition: opacity 0.3s ease-out;
            }
            &:hover &__overlay{
                opacity: 100;
            }
            &:hover p{
                opacity: 100;
            }
        }
    }
    &__secondText{
        padding-top: 180px;
        display: flex;
        justify-content: end;
        padding-right: 72px;
        &__wrap{
            p{
                @include poppins16_300();
                letter-spacing: 0.04em;
                max-width: 910px;
                color: var(--space-cadet-color);
            }
            &__button{
                width: fit-content;
                cursor: pointer;
                p{
                    @include poppins14_500();
                    letter-spacing: 0.1em;
                    padding-top: 48px;
                    text-transform: uppercase;
                    color: var(--iris-color);
                    &::after{
                        content: "";
                        height: 2px;
                        background-color: var(--iris-color);
                        width: 0px;
                        display: inherit;
                        transition: width 0.3s ease-out;
                    }
                    &:hover{
                        &::after{
                            width: 100%;
                        }
                    }
                }
            }
        }
    }
}