.hero{
    width: 100vw;
    height: 100vh;
    position: relative;
    &__bg{
        position: absolute;
        width: 100%;
        height: 100%;
        overflow: visible;
        z-index: -1;
    }
    &__content{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        &__circle{
            width: 960px;
            height: 960px;
            border-radius: 100%;
            background: #05051bf1;
            display: flex;
            justify-content: center;
            align-items: center;
            &__ring{
                width: 778px;
                height: 778px;
                border-radius: 100%;
                border: 2px solid var(--iris-color);
                display: flex;
                justify-content: center;
                align-items: center;
                &__text{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    flex-direction: column;
                    h1{
                        @include poppins56();
                        max-width: 634px;
                        text-align: center;
                        color: var(--white-color);
                        padding-bottom: 40px;
                        letter-spacing: 0.02em;
                    }
                    &__action{
                        display: flex;
                        gap: 16px;
                        cursor: pointer;
                        z-index: 1;
                        p{
                            @include poppins14_500();
                            color: var(--white-color); 
                            letter-spacing: 0.1em;
                            font-feature-settings: 'liga' off;
                            text-transform: uppercase;
                            /*
                            &::after{
                                content: "";
                                height: 2px;
                                background-color: white;
                                width: 100%;
                                left:50%;
                                display: inherit;
                                transition: transform 0.3s ease-out;
                                transform: scaleX(0);
                            }*/
                        }
                        &__button{
                            background-color: var(--iris-color);
                            transition: background-color 0.3s ease-out;
                            width: 28px;
                            height: 28px;
                            border-radius: 60px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                        }
                        &:hover &__button{
                            background-color: var(--iris-color-hover);
                        }
                        /*
                        &:hover p{
                            &::after{
                                transform: scaleX(1);
                            }
                        }*/
                    }
                }
            }
        }
    }
    &__shape{
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        translate: 108px;
        justify-content: center;
        top: 0px;
        z-index: 0;
    }
}