.nav{
    position: absolute;
    display: flex; 
    align-items: center; 
    justify-content: space-between;
    width: calc(100% - 80px);
    padding: 32px 40px 0px 40px;
    z-index: 9999;
    &__logo{
        width: 177px;
        height: 17.78px;
    }
    &__menu{
        display: flex;
        align-items: center;
        gap: 48px;
        &__item{
            @include poppins16_500();
            color: var(--white-color);
            text-align: center;
            letter-spacing: 0.08em;
            font-feature-settings: "liga" off;
            text-transform: uppercase;
            cursor: pointer;
            &::after{
                content: "";
                height: 2px;
                background-color: white;
                width: 0px;
                display: inherit;
                transition: width 0.3s ease-out;
            }
            &:hover{
                &::after{
                    width: 100%;
                }
            }
        }
        &__button{
            background-color: var(--iris-color);
            border-radius: 600px;
            cursor: pointer;
            transition: background-color 0.3s ease-out;
            p{
                padding: 14px 32px 14px 32px;
                @include poppins16_500();
                color: var(--white-color);
                text-transform: uppercase;
            }
            &:hover{
                background-color: var(--iris-color-hover);
            }
        }
    }
}